  }
  .ourcustomers__customers__costomer__man__inf__h1 {
    font-size: 18px;
    line-height: 25px;
  }
  .inf_design {
    padding-top: 20px;
  }
  footer {
    .socials {
      img {
        width: 30px;
        height: 30px;
      }
    }
    p {
      font-size: 14px;
      line-height: 16px;
    }
  }
}
